*{
    margin: 0;
    padding: 0;
}
body{
    background-color: rgb(35, 35, 35);
}
.topNav{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    padding: 30px 0;
    color: white;
    border-bottom: 3px solid white;
    a{
        color: white ;
        text-decoration: none;
    }
    p{
        margin-top: 20px;
    }
}
.container{
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 10px;
    text-align: center;
}
.search{
    padding: 100px 0 20px 0;
}
.input{
    padding: 10px; 
    width: 90%;
}
.card{
    width: 300px;
    background-color:blueviolet;
    color: white;
    padding: 50px;
    margin: 0 auto;
    margin-bottom: 100px;
    .card__title{
        font-size: 25px;
        border-bottom: 1px solid grey;
        padding-bottom: 20px;
    }
    .card__body{
        padding-top: 20px;
        border-top: 1px solid grey;
        h2{
            font-size: 50px;
        }
        h4{
            font-size: 20px;
            padding-top: 10px;
            color: blanchedalmond;
        }
    }
    @media  (max-width:425px) {
        width: 90%;
        padding: 10px;
    }
}